.container {
  margin: 50px 0;
}

.card {
  margin: 2% 2% !important; 
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23) !important;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1) !important;
  &:hover {
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23) !important;
  }
}

.Infected {
  border-bottom: 10px solid rgba(0, 0, 255, 0.5);
}

.Recovered {
  border-bottom: 10px solid rgba(0, 255, 0, 0.5);
}

.Deaths {
  border-bottom: 10px solid rgba(255, 0, 0, 0.5);
}

.Active {
  border-bottom: 10px solid rgba(75, 0, 130, 0.5);
}

.Closed {
  border-bottom: 10px solid rgba(255,165,0, 0.5);
}

@media (max-width: 770px) {
  .card {
    margin: 2% 0 !important;
  }
}

@media (max-width: 1024px) {
  .card {
    margin: 2% 2% !important;
  }
}